import updateMixin from '@/mixin/update'

require('./api/mock.js')

export default {
  mixins: [updateMixin],
  data () {
    return {
      model: {
        name: null,
        password: null,
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
    }
  },
}



